import React, {useContext} from"react"
import {Context} from "../App"
import "./email.css"
import Describe from "./describe"
import Prof from "./prof"
import { Formik } from 'formik';
import { useFormik } from 'formik';
import {useState} from "react"


function Email() {


  const val= useContext(Context)
  const validate = values => {
    const errors = {};
  
    if (!values.firstName) {
      errors.firstName = 'Give Git-Link';
    } else if (values.firstName.length > 15) {
      errors.firstName = 'Must be 15 characters or less';
    }

    if (!values.email) {
      errors.email = 'Please enter a valid email address';
    } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
      errors.email = 'Invalid email address';
    }
return errors;
  }

  const formik = useFormik({
    initialValues: {
      email: '',
      firstName:'',
    },
    validate,
    onSubmit: values => {
     val.setfire(true)
      val.setemail(values.email);
      val.setrender(<Prof/>)
      val.setcomplete(true)
      val.settarget("four")
     
   
      
      
      console.log(values.email);
    },
  });
   



  const nxtbtn={

    color:"white",
    backgroundColor: val.name==""&& val.email==""? "gray":  "#5b2993",
    position: "relative",
    top:"25px",
    left:"12px",
    width:"70px",
    padding: "2px",
    borderRadius: "10px"

  }




  return (
 <>
 
 <div className="email  animate__animated animate__backInRight animate__faster">
<span className="email_title" >Step 3/3</span>
<h4 className="email_h3">Cool {val.name=="" ? <span className="reqs" style={{display:"inline",position:"relative" ,top:"-1px",left:"-1px"}}>Name</span>: val.name}, where do we find you?</h4>
<p className="email_para">Fill in with your contact to get us into contact.</p>
 <form onSubmit={formik.handleSubmit}>
 <label className="email_label" for="name">What is your email?</label><br></br>
 {formik.touched.email && formik.errors.email ? (
         <span className="reqs">{formik.errors.email + "*"}</span>
       ) : null}
      <input
        id="email"
        name="email"
        className="input-vals"
        placeholder="absd@gmail.com"
        type="email"
        onChange={formik.handleChange}
         defaultValue={val.email}
      
      /><br></br>
         <label htmlFor="firstName" style={{display: 'block', padding:"4px 0"}}>What is your GitHub?</label>
         {formik.touched.firstName && formik.errors.firstName ? (
         <span className="reqs">{formik.errors.firstName + "*"}</span>
       ) : null}
       <input
         id="firstName"
         name="firstName"
         type="text"
         className="input-vals"
         onChange={formik.handleChange}
         onBlur={formik.handleBlur}
         value={formik.values.firstName}
         placeholder="https://github.com/andersonDias78 "
       />

       { val.email=="" ?val.name==""&& <span className="reqs">Complete all the Step</span>: null}
       <div className="bt-group"> 
      <span className="back-btn" onClick={()=> {
        val.setrender(<Describe/>)
        val.settarget(2)
      }}>Back</span>
<button type="submit" className="sub-btn" style={nxtbtn} disabled={val.name==""&& val.email==""? true:false} > Submit  </button>

    </div>
    </form>


<br></br>




  </div>
  
   
 
 
 
 
 
 
 
 
 
 
 </>
  )
}

export default Email



