
import "./card.css";
import Prof from "./prof"
import Center from "./Center"
import Describe from "./describe"
import Email from "./Email"
import Form from './Form';
import React, {useContext} from"react"
import {Context} from "../App"





function Card() {


  const val= useContext(Context)




const personal={

  backgroundColor:val.name==""? "#33842dad":"#5b2993"

};
const pro={

  backgroundColor: val.choice || val.choices ==true ? "#5b2993":"#33842dad" 

};
const mail={

  backgroundColor: val.email==""? "#33842dad":"#5b2993"

};
const done={

  backgroundColor: val.complete == false ? "#33842dad":"#5b2993"

};






  return (
    <>
    
<div className="total-card">
      
<div className="card-one" onClick={() => {
  val.settarget(1)
  val.setrender(<Form/>)
}}>
    <div class="card-text">
      <span className="card-h">Personal</span>
      <span className="card-sp">Identify yourself</span>
    </div>
    <div class="icons"style={personal}>
    <i  className="fa-solid fa-head-side-virus" ></i>
    </div>

    </div>
  {/* 2 */}
 
<div className="card-one" onClick={()=>{

val.settarget(2)
  val.setrender(<Describe/>)

}}>
    <div class="card-text card-custom-2">
      <span className="card-h">Professonal</span>
      <span className="card-sp" >Your level</span>
    </div>
    <div class="icons custom-icon-2" style={pro}>
    <i  className="fa-solid fa-newspaper " ></i>
    </div>
    </div>

{/* 3 */}
  <div className="card-one" onClick={() => {
    val.settarget(3)
  val.setrender(<Email/>)
  }}>
    

    <div class="card-text">
      <span  className="card-h" >Conatcts</span>
      <span className="card-sp">How to find you</span>
    </div>
    <div class="icons"style={mail}>
    <i  className="fa-solid fa-envelope"></i>
    </div>
  
  </div>

{/* 4 */}
<div className="card-one">
       

    <div class="card-text card-custom-2">
      <span className="card-h" >Complete</span>
      <span className="card-sp">Success</span>
    </div>
    <div class="icons custom-icon-2" style={done}>
    <i  className="fa-solid fa-circle-check"></i>
    </div>

  </div>

  </div>

    </>
  );
}

export default Card;
